			<!-- ËØ¶ÁªÜÂú∞ÂùÄËæìÂÖ• -->
			<view class="form-item">
				<text class="form-label">üìç ËØ¶ÁªÜÂú∞ÂùÄ</text>
				<textarea 
					v-model="formData.detailAddress" 
					class="form-textarea" 
					placeholder="ËØ∑ËæìÂÖ•ËØ¶ÁªÜÂú∞ÂùÄÔºåÂ¶ÇÔºö1ÂçïÂÖÉ101ÂÆ§" 
					maxlength="100"
					auto-height
				></textarea>
			</view>
			
			<!-- Â§áÊ≥®ËæìÂÖ• -->
			<view class="form-item">
				<text class="form-label">üí≠ Â§áÊ≥®‰ø°ÊÅØ<text class="optional">(ÂèØÈÄâ)</text></text>
				<textarea 
					v-model="formData.remarks" 
					class="form-textarea" 
					placeholder="Â¶ÇÊúâÁâπÊÆäÊÉÖÂÜµÂèØÂú®Ê≠§Â§áÊ≥®" 
					maxlength="200"
					auto-height
				></textarea>
			</view>
			
			<!-- Ë°®ÂçïÊìç‰ΩúÊåâÈíÆ -->
			<view class="form-actions">
				<button class="back-btn" @tap="backToPhoto">
					<text class="btn-icon">‚óÄÔ∏è</text>
					ËøîÂõû
				</button>
				<button class="confirm-btn" :class="{ 'recognizing': recognizing }" @tap="confirmAndRecognize">
					<text class="btn-icon">{{ recognizing ? '‚è≥' : '‚ú®' }}</text>
					{{ recognizing ? 'ËØÜÂà´‰∏≠...' : 'Á°ÆËÆ§ËØÜÂà´' }}
				</button>
			</view>
		</view>
		
		<!-- ‰ΩøÁî®ÊåáÂçó -->
		<view class="guide-card">
			<view class="guide-header">
				<text class="guide-icon">üí°</text>
				<text class="guide-title">ÊãçÊëÑÊåáÂçó</text>
			</view>
			
			<view class="guide-list">
				<view class="guide-item" v-for="(tip, index) in shootingTips" :key="index">
					<view class="tip-number">{{ index + 1 }}</view>
					<view class="tip-content">
