						<text class="tip-desc">{{ tip.desc }}</text>
					</view>
					<text class="tip-emoji">{{ tip.emoji }}</text>
				</view>
			</view>
		</view>
		
		<!-- ËØÜÂà´ËÆ∞ÂΩïÂç°Áâá -->
		<view class="stats-card">
			<view class="stats-header">
				<text class="stats-title">üìä ‰ªäÊó•ËØÜÂà´</text>
				<text class="stats-count">{{ todayCount }}</text>
			</view>
			<view class="stats-desc">
				<text>Á¥ØËÆ°‰∏∫ÊÇ®ËØÜÂà´ {{ totalCount }} Ê¨°ÁáÉÊ∞îË°®ËØªÊï∞</text>
			</view>
		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {
			imageUrl: '',
			recognizing: false,
			todayCount: 0,
			totalCount: 0,
			showForm: false,
			buildingIndex: 0,
			buildingOptions: ['1Âè∑Ê•º', '2Âè∑Ê•º', '3Âè∑Ê•º', '4Âè∑Ê•º', '5Âè∑Ê•º', '6Âè∑Ê•º', '7Âè∑Ê•º', '8Âè∑Ê•º', '9Âè∑Ê•º', '10Âè∑Ê•º', '11Âè∑Ê•º', '12Âè∑Ê•º'],
			formData: {
				building: '1Âè∑Ê•º',
				detailAddress: '',
				remarks: ''
			},
			shootingTips: [
				{
					title: 'ÂÖâÁ∫øÂÖÖË∂≥',
					desc: 'Á°Æ‰øùÁáÉÊ∞îË°®Êï∞Â≠óÊòæÁ§∫Ê∏ÖÊô∞',
					emoji: 'üí°'
				},
				{
					title: 'Ê∞¥Âπ≥ÊãçÊëÑ',
					desc: '‰øùÊåÅÊâãÊú∫‰∏éÁáÉÊ∞îË°®Âπ≥Ë°å',
					emoji: 'üìê'
				},
				{
					title: 'ÂÆåÊï¥ÊòæÁ§∫',
					desc: 'Á°Æ‰øùËØªÊï∞ÂÆåÂÖ®ÊòæÁ§∫Âú®ÁîªÈù¢‰∏≠',
					emoji: 'üéØ'
				},
				{
					title: 'ÈÅøÂÖçÂèçÂÖâ',
					desc: 'Èò≤Ê≠¢Èò¥ÂΩ±ÈÅÆÊå°Êï∞Â≠óÂå∫Âüü',
					emoji: 'üö´'
				},
				{
					title: 'Ê†∏ÂØπËØªÊï∞',
					desc: 'ËØÜÂà´ÂêéËØ∑È™åËØÅÁªìÊûúÂáÜÁ°ÆÊÄß',
					emoji: '‚úÖ'
				}
			]
		}
	},
	onLoad() {
		this.loadStatistics();
	},
	onShow() {
		this.loadStatistics();
	},
	methods: {
		// Âä†ËΩΩÁªüËÆ°Êï∞ÊçÆ
		async loadStatistics() {
			try {
				const gasMeter = uniCloud.importObject('gasMeter');
				const result = await gasMeter.getReports('ÂÖ®ÈÉ®');
				
				if (result.errCode === 0) {
					const reports = result.data;
					this.totalCount = reports.length;
					
					// ËÆ°ÁÆó‰ªäÊó•ËØÜÂà´Êï∞Èáè
					const today = new Date().toISOString().split('T')[0]; // YYYY-MM-DD
					const todayFormatted = today.replace(/-/g, '/'); // YYYY/M/D Ê†ºÂºè
					this.todayCount = reports.filter(report => {
						return report.date === todayFormatted || 
							   (report.updateTime && report.updateTime.startsWith(today));
					}).length;
				}
			} catch (error) {
				console.log('Âä†ËΩΩÁªüËÆ°Êï∞ÊçÆÂ§±Ë¥•:', error);
			}
		},
		// ÈÄâÊã©ÂõæÁâá
		chooseImage() {
			const that = this;
			uni.showActionSheet({
				itemList: ['ÊãçÁÖß', '‰ªéÁõ∏ÂÜåÈÄâÊã©'],
				success(res) {
					if (res.tapIndex === 0) {
						that.takePhoto();
					} else {
						that.chooseFromAlbum();
					}
				}
			});
		},
		
		// ÊãçÁÖß
		takePhoto() {
			const that = this;
			uni.chooseImage({
				count: 1,
				sourceType: ['camera'],
				success(res) {
					that.imageUrl = res.tempFilePaths[0];
					uni.showToast({
						title: 'ÊãçÁÖßÊàêÂäüÔºåËØ∑ÁªßÁª≠Â°´ÂÜô‰ø°ÊÅØ',
						icon: 'success',
						duration: 1500
					});
				},
				fail() {
					uni.showToast({
						title: 'ÊãçÁÖßÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
						icon: 'none',
						duration: 2000
					});
				}
			});
		},
		
		// ‰ªéÁõ∏ÂÜåÈÄâÊã©
		chooseFromAlbum() {
			const that = this;
			uni.chooseImage({
				count: 1,
				sourceType: ['album'],
				success(res) {
					that.imageUrl = res.tempFilePaths[0];
					uni.showToast({
						title: 'ÂõæÁâáÈÄâÊã©ÊàêÂäüÔºåËØ∑ÁªßÁª≠Â°´ÂÜô‰ø°ÊÅØ',
						icon: 'success',
						duration: 1500
					});
				},
				fail() {
					uni.showToast({
						title: 'ÈÄâÊã©ÂõæÁâáÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
						icon: 'none',
						duration: 2000
					});
				}
			});
		},
		
		// Ê∏ÖÈô§ÂõæÁâá
		clearImage() {
			this.imageUrl = '';
			this.showForm = false;
			// ÈáçÁΩÆË°®ÂçïÊï∞ÊçÆ
			this.formData = {
				building: '1Âè∑Ê•º',
				detailAddress: '',
				remarks: ''
			};
			this.buildingIndex = 0;
		},
		
		// ÊòæÁ§∫‰ø°ÊÅØÂ°´ÂÜôË°®Âçï
		showInfoForm() {
			this.showForm = true;
		},
		
		// ËøîÂõûÁÖßÁâáÈÄâÊã©
		backToPhoto() {
			this.showForm = false;
		},
		
		// Ê•ºÊ†ãÈÄâÊã©ÂèòÂåñ
		onBuildingChange(e) {
			this.buildingIndex = e.detail.value;
			this.formData.building = this.buildingOptions[e.detail.value];
		},
		
		// Á°ÆËÆ§‰ø°ÊÅØÂπ∂ËØÜÂà´ÂõæÁâá
		async confirmAndRecognize() {
			if (!this.imageUrl) {
				uni.showToast({
					title: 'ËØ∑ÂÖàÈÄâÊã©ÂõæÁâá',
					icon: 'none'
				});
				return;
			}
			
			// È™åËØÅË°®ÂçïÊï∞ÊçÆ
			if (!this.formData.detailAddress.trim()) {
				uni.showToast({
					title: 'ËØ∑Â°´ÂÜôËØ¶ÁªÜÂú∞ÂùÄ‰ø°ÊÅØ',
					icon: 'none',
					duration: 2000
				});
				return;
			}
			
			this.recognizing = true;
			
			try {
				// Â∞ÜÂõæÁâáËΩ¨Êç¢‰∏∫base64
				const imageBase64 = await this.convertImageToBase64(this.imageUrl);
				
				// ‰∏ä‰º†ÂõæÁâáÂà∞‰∫ëÂ≠òÂÇ®Âπ∂Ëé∑ÂæóURL
				let uploadedImageUrl = '';
				try {
					const uploadResult = await uniCloud.uploadFile({
						filePath: this.imageUrl,
						cloudPath: `gas-meter-images/${Date.now()}-${Math.random().toString(36).substr(2, 9)}.jpg`,
						cloudPathAsRealPath: true
					});
					uploadedImageUrl = uploadResult.fileID;
				} catch (uploadError) {
					console.log('ÂõæÁâá‰∏ä‰º†Â§±Ë¥•ÔºåÁªßÁª≠ËøõË°åËØÜÂà´‰ΩÜ‰∏ç‰øùÂ≠òÂõæÁâáURL:', uploadError);
				}
				
				// Ë∞ÉÁî®‰∫ëÂØπË±°ËøõË°åËØÜÂà´
				const gasMeter = uniCloud.importObject('gasMeter');
				// ÊûÑÂª∫ÂÆåÊï¥Âú∞ÂùÄ
				const fullAddress = `${this.formData.building} ${this.formData.detailAddress}`;
				const result = await gasMeter.recognizeGasMeter(
					imageBase64, 
					fullAddress, 
					this.formData.building,
					this.formData.remarks,
					uploadedImageUrl
				);
				
				this.recognizing = false;
				
				if (result.errCode === 0) {
					// ËØÜÂà´ÊàêÂäü
					this.todayCount++;
					this.totalCount++;
					
					uni.showToast({
						title: 'üéâ ËØÜÂà´ÊàêÂäüÔºÅÊ≠£Âú®ÁîüÊàêÊä•Âëä...',
						icon: 'success',
						duration: 2000
					});
					
					// ‰øùÂ≠òÂΩìÂâçË°®ÂçïÊï∞ÊçÆÔºåÁî®‰∫éÈ°µÈù¢Ë∑≥ËΩ¨
					const currentImageUrl = this.imageUrl;
					const currentRemarks = this.formData.remarks;
					
					// ÈáçÁΩÆÁä∂ÊÄÅ
					this.showForm = false;
					this.imageUrl = '';
					this.formData = {
						building: '1Âè∑Ê•º',
						detailAddress: '',
						remarks: ''
					};
					this.buildingIndex = 0;
					
					// Âª∂ËøüË∑≥ËΩ¨ÔºåËÆ©Áî®Êà∑ÁúãÂà∞ÊàêÂäüÊèêÁ§∫
					setTimeout(() => {
						uni.navigateTo({
							url: `/pages/reportDetail/reportDetail?reportId=${result.data.reportId}&reading=${result.data.reading}&address=${result.data.address}&date=${result.data.date}&time=${result.data.time}&imageUrl=${encodeURIComponent(currentImageUrl)}&integerPart=${result.data.integerPart}&decimalPart=${result.data.decimalPart}&remarks=${encodeURIComponent(currentRemarks)}`
						});
					}, 1500);
					
				} else {
					// ËØÜÂà´Â§±Ë¥•
					uni.showModal({
						title: 'ËØÜÂà´Â§±Ë¥•',
						content: result.errMsg || 'Êó†Ê≥ïËØÜÂà´ÁáÉÊ∞îË°®ËØªÊï∞ÔºåËØ∑Ê£ÄÊü•ÂõæÁâáÊòØÂê¶Ê∏ÖÊô∞ÔºåÁ°Æ‰øùÊï∞Â≠óÊòæÁ§∫ÂÆåÊï¥„ÄÇ',
						confirmText: 'ÈáçÊñ∞ÊãçÁÖß',
						showCancel: true,
						cancelText: 'Á®çÂêéÂÜçËØï',
						success: (modalRes) => {
							if (modalRes.confirm) {
								// Ê∏ÖÈô§ÂΩìÂâçÂõæÁâáÔºåËÆ©Áî®Êà∑ÈáçÊñ∞ÊãçÁÖß
								this.clearImage();
							}
						}
					});
				}
				
			} catch (error) {
				this.recognizing = false;
				console.error('ËØÜÂà´Âá∫Èîô:', error);
				
				uni.showModal({
					title: 'ÁΩëÁªúÈîôËØØ',
					content: 'ËØÜÂà´ËøáÁ®ã‰∏≠ÁΩëÁªúÂºÇÂ∏∏ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•ÂêéÈáçËØï„ÄÇ',
					confirmText: 'ÈáçÊñ∞ËØÜÂà´',
					showCancel: true,
					cancelText: 'Á®çÂêéÂÜçËØï',
					success: (modalRes) => {
						if (modalRes.confirm) {
							// ËÆ©Áî®Êà∑ÈáçÊñ∞ËØÜÂà´
							this.confirmAndRecognize();
						}
					}
				});
			}
		},
		
		// Â∞ÜÂõæÁâáËΩ¨Êç¢‰∏∫base64ÁºñÁ†Å
		convertImageToBase64(imagePath) {
			return new Promise((resolve, reject) => {
				uni.getFileSystemManager().readFile({
					filePath: imagePath,
					encoding: 'base64',
					success: (res) => {
						resolve(res.data);
					},
					fail: (error) => {
						reject(error);
					}
				});
			});
		},
		
	}
}
</script>

